import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./manuscriptComponents/common/Header/Header";
import Footer from "./manuscriptComponents/common/Footer/Footer";
import ManuReview from "./manuscriptComponents/ManuReview/ManuReview";
import ManuPass from "./manuscriptComponents/ManuPass/ManuPass";

//页面路由
const ManuRouter = () => {
  return (
    <div className="manuscript-wrapper">
      <Router>
        <Header />
        <Route exact path="/" component={ManuReview} />
        <Route path="/manureview/" component={ManuReview} />
        <Route path="/manupass/" component={ManuPass} />
        <Footer />
      </Router>
    </div>
  );
};

export default ManuRouter;
