import 'braft-editor/dist/index.css'
import React, { Component } from 'react';
import BraftEditor from 'braft-editor'
import { ContentUtils } from 'braft-utils'
// import { ImageUtils } from 'braft-finder'
import { Input, Button, Select,  Space, Upload, } from 'antd'
import 'antd/dist/antd.css';
import 'react-flexbox-layout/lib/styles.css';
import '../styles/richEditor.css';
import {
  PictureOutlined,
  // VideoCameraAddOutlined,
  // AudioOutlined
} from '@ant-design/icons';

export default class Editor extends Component {
  constructor(props) {
    super(props)
    this.state = {
      editorState: BraftEditor.createEditorState(''), // 设置编辑器初始内容
      outputHTML: '<p></p>',
      selectValue: '',
      inputTitleValue: '',
      inputAuthorValue: '',
      inputEditorLengthValue: 0
    }
  }

  preview = () => {

    if (window.previewWindow) {
      window.previewWindow.close()
    }

    window.previewWindow = window.open()
    window.previewWindow.document.write(this.buildPreviewHtml())
    window.previewWindow.document.close()

  }

  buildPreviewHtml() {

    return `
    <!Doctype html>
    <html>
      <head>
        <title>Preview Content</title>
        <style>
          html,body{
            height: 100%;
            margin: 0;
            padding: 0;
            overflow: auto;
            background-color: #f1f2f3;
          }
          .container{
            box-sizing: border-box;
            width: 1000px;
            max-width: 100%;
            min-height: 100%;
            margin: 0 auto;
            padding: 30px 20px;
            overflow: hidden;
            background-color: #fff;
            border-right: solid 1px #eee;
            border-left: solid 1px #eee;
          }
          .container img,
          .container audio,
          .container video{
            max-width: 100%;
            height: auto;
          }
          .container p{
            white-space: pre-wrap;
            min-height: 1em;
          }
          .container pre{
            padding: 15px;
            background-color: #f1f1f1;
            border-radius: 5px;
          }
          .container blockquote{
            margin: 0;
            padding: 15px;
            background-color: #f1f1f1;
            border-left: 3px solid #d1d1d1;
          }
        </style>
      </head>
      <body>
        <div class="container">${this.state.editorState.toHTML()}</div>
      </body>
    </html>
  `

  }

  componentDidMount() {
    this.isLivinig = true
    // 3秒后更改编辑器内容
    setTimeout(this.setEditorContentAsync, 3000)
  }

  componentWillUnmount() {
    this.isLivinig = false
  }

  handleChange = (editorState) => {
    // let num = editorState.getCurrentContent().getPlainText('').length
    // let num = editorState.toText().length
    let num1 = this.wordStatic(editorState.toText())
    this.setState({
      editorState: editorState,
      inputEditorLengthValue: num1,
      // outputHTML: editorState.toHTML()
    })
  }
  wordStatic = (str) => {
    // console.log(str.length)
    var repOne = /[a-zA-Z]/  // 字母数字 
    // var repMany = /^[a-zA-Z0-9]+$/g
       
    // str = str.replace(repMany, ' ')
    console.log(str)
    str = str.replace(/[\r\n|\s]/ig, '')
    
    var strAttr = str.split('')
    var len = 0
    strAttr.forEach(function (val, key) {
      // 字母数字空格
      if (repOne.test(val)) {
       
        len += 0.5 // 统计 占位0.5个中文
       }
      else {
        // 中文或者语言其他符号
        len++ 
       }
    })
    
  return Math.ceil(len)
}
  handleSelectChange = (selectValue) => {
    this.setState({ selectValue: selectValue });
  }
  handleInputChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  handleSave = () => {
    // 在编辑器获得焦点时按下ctrl+s会执行此方法
    // 编辑器内容提交到服务端之前，可直接调用editorState.toHTML()来获取HTML格式的内容
    const htmlContent = this.state.editorState.toHTML()
    const submitData = {
      inputTitleValue: this.state.inputTitleValue,
      inputAuthorValue: this.state.inputAuthorValue,
      selectValue: this.state.selectValue,
      editorState: this.state.editorState.toRAW() // or values.content.toHTML()
    }
    console.log(submitData)
    this.setState({
      outputHTML: this.state.editorState.toHTML()
    })
    // const result = await saveEditorContent(htmlContent)
  }
  handleSubmit = () => {
    // 在编辑器获得焦点时按下ctrl+s会执行此方法
    // 编辑器内容提交到服务端之前，可直接调用editorState.toHTML()来获取HTML格式的内容
    const htmlContent = this.state.editorState.toHTML()
    const submitData = {
      inputTitleValue: this.state.inputTitleValue,
      inputAuthorValue: this.state.inputAuthorValue,

      editorState: this.state.editorState.toRAW() // or values.content.toHTML()
    }
    console.log(submitData)
    this.setState({
      outputHTML: this.state.editorState.toHTML()
    })
    // const result = await saveEditorContent(htmlContent)
  }

  uploadHandler = (param) => {

    if (!param.file) {
      return false
    }
    console.log(param)
    this.setState({
      editorState: ContentUtils.insertMedias(this.state.editorState, [{
        type: 'IMAGE',
        url: URL.createObjectURL(param.file)
      }])
    })

  }

  uploadAudioHandler = (param) => {

    if (!param.file) {
      return false
    }
    console.log(param)
    this.setState({
      editorState: ContentUtils.insertMedias(this.state.editorState, [{
        type: 'AUDIO',
        url: URL.createObjectURL(param.file)
      }])
    })

  }

  uploadVideoHandler = (param) => {

    if (!param.file) {
      return false
    }
    console.log(param)

    this.setState({
      editorState: ContentUtils.insertMedias(this.state.editorState, [{
        type: 'VIDEO',
        url: URL.createObjectURL(param.file)
      }])
    })

  }

  myUploadFn1 = (param) => {

    console.log('param', param.file);
    //const serverURL = 'https://www.mocky.io/v3/428de921-a2b6-4c96-aab8-afb6e58034b8';//upload 是接口地址
    const serverURL = 'https://www.mocky.io/v3/de3dc3b3-7120-4bcb-8531-6102584396de'
    const xhr = new XMLHttpRequest();
    const fd = new FormData();

    const successFn = (response) => {
      // 假设服务端直接返回文件上传后的地址
      // 上传成功后调用param.success并传入上传后的文件地址
      //console.log('response', response.currentTarget);
      //console.log('xhr.responseText', xhr.responseText);
      const upLoadObject = JSON.parse(response && response.currentTarget && response.currentTarget.response);
      param.success({
        url: JSON.parse(xhr.responseText).url,
        meta: {
          id: upLoadObject && upLoadObject.id,
          title: upLoadObject && upLoadObject.fileName,
          alt: upLoadObject && upLoadObject.fileName,
          loop: false, // 指定音视频是否循环播放
          autoPlay: false, // 指定音视频是否自动播放
          controls: false, // 指定音视频是否显示控制栏
          poster: '', // 指定视频播放器的封面
        }
      })
    };

    const progressFn = (event) => {
      // 上传进度发生变化时调用param.progress
      param.progress(event.loaded / event.total * 100)

    };

    const errorFn = (response) => {
      // 上传发生错误时调用param.error
      param.error({
        msg: 'unable to upload.'
      })
    };

    xhr.upload.addEventListener("progress", progressFn, false);
    xhr.addEventListener("load", successFn, false);
    xhr.addEventListener("error", errorFn, false);
    xhr.addEventListener("abort", errorFn, false);

    fd.append('file', param.file);
    xhr.open('POST', serverURL, true);
    xhr.send(fd)

  };

  myUploadFn = (param) => {

    const serverURL = 'https://www.mocky.io/v3/de3dc3b3-7120-4bcb-8531-6102584396de'
    const xhr = new XMLHttpRequest()
    const fd = new FormData()

    const successFn = (response) => {
      // 假设服务端直接返回文件上传后的地址
      // 上传成功后调用param.success并传入上传后的文件地址
      console.log("xhr", xhr);
      param.success({
        url: JSON.parse(xhr.responseText).url,
        meta: {
          id: 'img' + Math.random() * 10,
          title: '上传文件',
          alt: '上传文件',
          loop: true, // 指定音视频是否循环播放
          autoPlay: true, // 指定音视频是否自动播放
          controls: true, // 指定音视频是否显示控制栏
          poster: 'http://xxx/xx.png', // 指定视频播放器的封面
        }
      })

    }

    const progressFn = (event) => {
      // 上传进度发生变化时调用param.progress
      param.progress(event.loaded / event.total * 100)
    }

    const errorFn = (response) => {
      // 上传发生错误时调用param.error
      param.error({
        msg: '上传失败！'
      })
    }

    xhr.upload.addEventListener("progress", progressFn, false)
    xhr.addEventListener("load", successFn, false)
    xhr.addEventListener("error", errorFn, false)
    xhr.addEventListener("abort", errorFn, false)

    fd.append('file', param.file)
    xhr.open('POST', serverURL, true)
    xhr.send(fd)
    console.log("fd", fd, param.file);
  }
  //  校验不通过的媒体文件将不会被添加到媒体库中
  myValidateFn = (file) => {
    // return file.size < 1024 * 100 ? message.success("图片上传成功！") : message.warn("图片太大，请上传合适大小的图片！");
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        file.size < 1024 * 100 ? resolve() : reject()
      }, 2000)
    })
  }

  myUploadFn2 = (param) => {
    const serverURL = 'https://www.mocky.io/v3/de3dc3b3-7120-4bcb-8531-6102584396de'
    const xhr = new XMLHttpRequest()
    const fd = new FormData()
    console.log(fd)
    console.log(xhr)

    const successFn = (response) => {
      // 假设服务端直接返回文件上传后的地址
      // 上传成功后调用param.success并传入上传后的文件地址，请根据后端返回值自行设置
      param.success({
        url: JSON.parse(xhr.responseText).url
      })
    }

    const progressFn = (event) => {
      // 上传进度发生变化时调用param.progress
      param.progress(event.loaded / event.total * 100)
    }

    const errorFn = (response) => {
      // 上传发生错误时调用param.error
      param.error({
        msg: 'unable to upload.'
      })
    }
    xhr.upload.addEventListener('progress', progressFn, false)
    xhr.addEventListener('load', successFn, false)
    xhr.addEventListener('error', errorFn, false)
    xhr.addEventListener('abort', errorFn, false)

    fd.append('upload', param.file)
    xhr.open('POST', serverURL, true)
    xhr.setRequestHeader('token', 'be4cdf198a7a8c06af23e0b2b60d637d')
    xhr.send(fd)
  }

  render() {
    const fontFamily = [{
      name: "宋体",
      family: '"宋体",sans-serif'
    }, {
      name: 'Araial',
      family: 'Arial, Helvetica, sans-serif'
    }, {
      name: 'Georgia',
      family: 'Georgia, serif'
    }, {
      name: 'Impact',
      family: 'Impact, serif'
    }, {
      name: 'Monospace',
      family: '"Courier New", Courier, monospace'
    }, {
      name: 'Tahoma',
      family: 'tahoma, arial, "Hiragino Sans GB", 宋体, sans-serif'
    }, {
      name: '黑体',
      family: '"黑体",serif'
    }, {
      name: '楷体',
      family: '楷体'
    }, {
      name: '幼圆',
      family: 'YouYuan'
    }]
    const controls = ['font-family',
      'font-size', 'headings', 'separator',
      'text-color', 'bold', 'italic', 'underline', 'separator',
      'text-align', 'list-ol',
      'list-ul', 'separator',
      'blockquote',
      'link',
      'media',
      'fullscreen',]

    const extendControls = [

      {
        key: 'img-uploader',
        type: 'component',
        component: (
          <Upload
            accept="image/*"
            showUploadList={false}
            customRequest={this.uploadHandler}
          >
            {/* 这里的按钮最好加上type="button"，以避免在表单容器中触发表单提交，用Antd的Button组件则无需如此 */}
            <button type="button" className="control-item button upload-button" data-title="插入图片">
              <PictureOutlined />
            </button>
          </Upload>

        )
      },
      // {
      //   key: 'audio-uploader',
      //   type: 'component',
      //   component: (
      //     <Upload 
      //       accept="audio/*"
      //       showUploadList={false}
      //       customRequest={this.uploadAudioHandler}
      //     >
      //       {/* 这里的按钮最好加上type="button"，以避免在表单容器中触发表单提交，用Antd的Button组件则无需如此 */}
      //       <button type="button" className="control-item button upload-audio-button" data-title="插入音频">
      //         <AudioOutlined />
      //       </button>
      //     </Upload>
      //   )
      // },
      // {
      //   key: 'video-uploader',
      //   type: 'component',
      //   component: (
      //     <Upload 
      //       accept="video/*"
      //       showUploadList={false}
      //       customRequest={this.uploadVideoHandler}
      //     >
      //       {/* 这里的按钮最好加上type="button"，以避免在表单容器中触发表单提交，用Antd的Button组件则无需如此 */}
      //       <button type="button" className="control-item button upload-audio-button" data-title="插入视频">
      //         <VideoCameraAddOutlined />
      //       </button>
      //     </Upload>
      //   )
      // }

    ]

    const media = {
      uploadFn: this.myUploadFn1,
      // validateFn:this.myValidateFn,
      accepts: {
        image: 'image/png,image/jpeg,image/gif,image/webp,image/apng,image/svg',
        video: 'video/mp4',
        audio: 'audio/mp3'
      },
      externals: {
        image: true,
        video: true,
        audio: true,
        embed: false
      },
      allowPasteImage: true
    }
    const { Option } = Select;
    const { editorState, outputHTML, selectValue, inputTitleValue, inputAuthorValue, inputEditorLengthValue } = this.state

    return (
      <div>
        <div className="editor-wrapper">

          <BraftEditor className="my-editor"
            fontFamilies={fontFamily}
            media={media}
            controls={controls}
            extendControls={extendControls}
            value={editorState}
            onChange={this.handleChange}
            onInput={val => { console.log(val) }}
            componentBelowControlBar={
              <div className='input-color'>
                <Input className='input-background' placeholder="新闻标题"
                  name='inputTitleValue'
                  type="text" bordered={false} value={inputTitleValue} onChange={this.handleInputChange}
                />
                <Input className='input-background' style={{ width: 120 }} placeholder="作者"
                  name='inputAuthorValue'
                  bordered={false} value={inputAuthorValue} onChange={this.handleInputChange}
                />
              </div>
            }
            contentStyle={{ height: 340, backgroundColor: 'rgba(150, 150, 150, 0.2)', boxShadow: 'inset 0 1px 3px rgba(0,0,0,.1)' }}

          />
          <Space >
            <span className='text-length'><label>正文字数：{inputEditorLengthValue}</label> </span>
            <span className='select'>
              <label>推送对象：</label>
              <Select value={selectValue} style={{ width: 120 }} onChange={this.handleSelectChange}
                autoClearSearchValue={true}
              >
                <Option value="group1">group1</Option>
                <Option value="group2">group2</Option>
                <Option value="group3">group3</Option>
              </Select>

            </span>
            <Button type="primary" htmlType="button" className='button-color'
              onClick={this.handleSave}
            >保存</Button>
            <Button type="primary" htmlType="button" className='button-color'
              onClick={this.preview}
            >预览</Button>
            <Button type="primary" htmlType="submit" className='button-color'
              onClick={this.handleSubmit}
            >推送</Button>

          </Space>

        </div>
        {/* <h5>输出内容</h5>
        <div className="output-content">{outputHTML}</div> */}
      </div>
    )
  }
}
