import React, { Component } from "react";

import Header from "../common/Header/Header";
import Footer from "../common/Footer/Footer";
import { Card } from "antd";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import "./ManuReview.css";

//已发布页面
export default class ManuReview extends Component {
  render() {
    return (
      <div className="manucontent">
        <div className="manucontent-article">
          <Card title="共找到20篇符合条件的内容">
            <Card
              className="clearfix"
              type="inner"
              title="Inner Card title"
              extra={
                <a href="#">
                  <EditOutlined />
                  删除
                </a>
              }
            >
              Inner Card content
              <a href="#">
                <DeleteOutlined />
                删除
              </a>
            </Card>
            <Card
              style={{ marginTop: 16 }}
              type="inner"
              title="Inner Card title"
              extra={
                <a href="#">
                  <EditOutlined />
                  删除
                </a>
              }
            >
              Inner Card content
              <a href="#">
                <DeleteOutlined />
                删除
              </a>
            </Card>
            <Card
              style={{ marginTop: 16 }}
              type="inner"
              title="Inner Card title"
              extra={
                <a href="#">
                  <EditOutlined />
                  删除
                </a>
              }
            >
              Inner Card content
              <a href="#">
                <DeleteOutlined />
                删除
              </a>
            </Card>
          </Card>
        </div>
      </div>
    );
  }
}
