import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { Input, DatePicker, Space } from "antd";
import "./Header.css";

const Header = () => {
  return (
    <div className="manuheader">
      <div className="manuheader-status">
          <ul>
            <li>稿件状态</li>
            <li>
              <a href="#">全部</a>
            </li>
            <li>
              <Link to='/manureview/'>已发布</Link>
              {/* <a href="#">已发布</a>   */}
            </li>
            <li>
              <Link to='/manupass/'>未通过</Link>
              {/* <a href="#">未通过</a> */}
            </li>
            <li>
              <a href="#">已保存</a>
            </li>
          </ul>         
        <Input.Search
          placeholder="请输入标题关键字"
          onSearch={(value) => console.log(value)}
          enterButton
        />
      </div>
      <div className="manuheader-time">
        <ul>
          <li>时间选择</li>
          <li>
            <Space direction="vertical" size={12}>
              <DatePicker.RangePicker />
            </Space>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Header;
